import axios from 'axios';
import React, { useState } from 'react';
import $ from 'jquery';


function App(props) {

  const [name, setName] = useState("변프로"); 
  const [ans, setAns] = useState(0);
  const [history, setHistory] = useState([]);

let f1 = ()=>{
    console.log(" 여기로는 들어 오나 ?");
    
      axios.get("/request", {} )
    .then( (res)=>{

         console.log("axios로 받아온 Data:" + res.data.factorA, res.data.factorB);
         //아래 controller의 span 의 객체를 땡겨와서
         let s1 = document.getElementById("span1");
         let s2 = document.getElementById("span2");
         // span 객체에 ajax로 받아온 Data 대입
         s1.textContent = res.data.factorA;
         s2.textContent = res.data.factorB;
      } );
   }
   
let   f2 = ()=>{

      // client 가 입력한 Data 땡기기
      let a = $("#span1").text();
      let b = $("#span2").text();
      console.log(a, b);
      
      // 닉네임
      let alias1 = $('input[name=alias]').val();
      console.log(alias1);
      
      //답안
      let attempt1 = $('input[name=attempt]').val();
      console.log(attempt1);
      
      // 보낼 Data 객체 형식
      let formData = {
            user: {alias: alias1},
            multiplication : {factorA: a, factorB: b},
            resultAttempt: attempt1
      };

      
    axios.post("/send", formData)
    .then( (res)=>{
      let s3 = document.getElementById("span3");
      s3.textContent = res.data;
      let temp = {
        name : name,
        ques : formData.multiplication.factorA + "*" + formData.multiplication.factorB,
        ans : formData.resultAttempt
      }
      setHistory((history)=>[...history, temp]);
      console.log(history);
    });    
}

    return (
      <div>
        <h1> this is index </h1>
        <button onClick = {f1}> 문제요청 </button> <br/>
        <span id="span1"></span> 곱하기
        <span id="span2"></span> 는?

        <form id="form1" name="form1" method="post">
          이름입력 : <input type="text" name="alias" onChange={ (e)=>{setName(e.target.value)}}/> <br/>
          Answer : <input type="text" name="attempt" onChange={ (e)=>{setAns(e.target.value)}} /> <br/>
          <input type="button" value="전송" onClick={f2}/>
        </form>
        <span id="span3"></span>
      </div>
    );
}
  
export default App;